import React, {useContext, useState} from 'react'
import { Link } from 'react-router-dom';
import Search from './Search'
import bannerImage from '../assets/img/logo/banner2.jpg';
import TypeWriter from 'typewriter-effect';
import { HouseContext } from './HouseContext';
import House from './House';
import { ImSpinner10 } from 'react-icons/im';



import CountryDropdown from './CountryDropdown';
import PropertyDropdown from './PropertyDropdown';
import PriceRangeDropdown from './PriceRangeDropdown';

import {RiSearch2Line} from 'react-icons/ri';

const Buy = () => {
  const {handleClick} = useContext(HouseContext)
  const { houses, loading } = useContext(HouseContext);
  const [searchValue, setSearchValue] = useState("");
  const [isLoading, setIsLoading] = useState(false);
  const handleSearchFilter = (event) => {
    setSearchValue(event.target.value);
  };

  const filteredHouses = houses.filter((house) =>
    house.country && house.address.toLowerCase().includes(searchValue.toLowerCase())
  );

  if (loading) {
    return (
      <ImSpinner10 className='mx-auto animate-spin text-blue-500 text-4xl mt-[300px] text-[72px]' />
    );
  }

  return (
    <>
       <section className="back bg-gray-800 dark:bg-gray-800 text-white dark:text-gray-100 mt-5 max-auto" style={{ 
        backgroundImage: `url(${bannerImage})`,
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center",
        backgroundSize: "cover",
        height: "50vh",
        width: "100%",
        }}>

      <div className="container mx-auto mt-10 h-full flex flex-col justify-center">
        <div className="items-center">
          <h1 className=' font-semibold text-center mx-auto text-5xl md:text-7xl lg:text-8xl'>
            Your Compass in
          </h1>
          <h1 className=' font-semibold text-center mx-auto text-5xl md:text-7xl lg:text-8xl'>
            Real Estate
          </h1>



          <div className="flex justify-center mt-6 space-x-4">
            <Link to='/buy'>
            <button
                className="px-4 py-2 font-bold tracking-wide text-red-500 capitalize transition-colors duration-200 transform bg-white border border-green-500 rounded-md hover:bg-green-50 focus:outline-none focus:bg-blue-50 w-44 flex items-center justify-center"
                onClick={() => {
                  setIsLoading(true);
                  handleClick();
                  setTimeout(() => {
                    setIsLoading(false);
                  }, 1000);
                }}
              >
                {isLoading ? (
                  <RiSearch2Line className="animate-spin mr-2" />
                ) : (
                  <>
                    <span className="mr-2">BUY</span>
                    <svg
                      xmlns="http://www.w3.org/2000/svg"
                      fill="none"
                      viewBox="0 0 24 24"
                      strokeWidth={1.5}
                      stroke="currentColor"
                      className="w-6 h-6"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        d="M15.75 10.5V6a3.75 3.75 0 10-7.5 0v4.5m11.356-1.993l1.263 12c.07.665-.45 1.243-1.119 1.243H4.25a1.125 1.125 0 01-1.12-1.243l1.264-12A1.125 1.125 0 015.513 7.5h12.974c.576 0 1.059.435 1.119 1.007zM8.625 10.5a.375.375 0 11-.75 0 .375.375 0 01.75 0zm7.5 0a.375.375 0 11-.75 0 .375.375 0 01.75 0z"
                      />
                    </svg>
                  </>
                )}
              </button>
            </Link>
            <Link to='/rent'>
            <button
                className="px-4 py-2 font-bold tracking-wide text-red-500 capitalize transition-colors duration-200 transform bg-white border border-green-500 rounded-md hover:bg-green-50 focus:outline-none focus:bg-blue-50 w-44 flex items-center justify-center"
                onClick={() => {
                  setIsLoading(true);
                  handleClick();
                  setTimeout(() => {
                    setIsLoading(false);
                  }, 1000);
                }}
              >
                {isLoading ? (
                  <RiSearch2Line className="animate-spin mr-2" />
                ) : (
                  <>
                    <span className="mr-2">RENT</span>
                    <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" strokeWidth={1.5} stroke="currentColor" className="w-6 h-6">
                      <path strokeLinecap="round" strokeLinejoin="round" d="M9 12h3.75M9 15h3.75M9 18h3.75m3 .75H18a2.25 2.25 0 002.25-2.25V6.108c0-1.135-.845-2.098-1.976-2.192a48.424 48.424 0 00-1.123-.08m-5.801 0c-.065.21-.1.433-.1.664 0 .414.336.75.75.75h4.5a.75.75 0 00.75-.75 2.25 2.25 0 00-.1-.664m-5.8 0A2.251 2.251 0 0113.5 2.25H15c1.012 0 1.867.668 2.15 1.586m-5.8 0c-.376.023-.75.05-1.124.08C9.095 4.01 8.25 4.973 8.25 6.108V8.25m0 0H4.875c-.621 0-1.125.504-1.125 1.125v11.25c0 .621.504 1.125 1.125 1.125h9.75c.621 0 1.125-.504 1.125-1.125V9.375c0-.621-.504-1.125-1.125-1.125H8.25zM6.75 12h.008v.008H6.75V12zm0 3h.008v.008H6.75V15zm0 3h.008v.008H6.75V18z" />
                    </svg>
                  </>
                )}
              </button>
            </Link>

          </div>
        </div>
        <div className='text-center text-4xl lg:text-[37px] font-semibold mt-5'>
            <TypeWriter
              options ={{
                strings: ['Rent Properties' ],
                autoStart: true,
                loop: true.valueOf,
              }}
            />
          </div>
      </div>
    </section>
    <div className="mx-auto">
        <p className='font-bold'>Filter settings</p>
        <div className='px-[15px] py-2 max-w-[1440px] mx-auto flex flex-col lg:flex-row justify-between gap-4 lg:gap-x-3 relative lg:top-100 lg:shadow-1 bg-white rounded-lg md:border-4'>
          <CountryDropdown />
          <PropertyDropdown />
          <PriceRangeDropdown />
          <button  onClick={()=> handleClick()} className='bg-orange-700 hover:bg-blue-800 transition w-full lg:max-w-[120px] h-50 rounded-lg flex justify-center items-center text-white text-lg'>
            Search &nbsp; <RiSearch2Line  /> 
          </button>
        </div>
      </div>

    <div style={{ display: "flex", justifyContent: "center" }} className='mt-10'>
  <div
    style={{
      display: "flex",
      alignItems: "center",
      justifyContent: "center",
      padding: "10px",
      borderTopLeftRadius: "5px",
      borderBottomLeftRadius: "5px",
      border: "1px solid #000",
      background: "#fff",
      width: "50px",
    }}
  >
    <svg
      xmlns="http://www.w3.org/2000/svg"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
      style={{ width: "20px", height: "20px" }}
    >
      <circle cx="11" cy="11" r="8" />
      <line x1="21" y1="21" x2="16.65" y2="16.65" />
    </svg>
  </div>
  <input
    type="search"
    placeholder="Search Location, Property, etc..."
    value={searchValue}
    onChange={handleSearchFilter}
    style={{
      display: "block",
      padding: "10px",
      borderTopRightRadius: "5px",
      borderBottomRightRadius: "5px",
      border: "1px solid #000",
      fontSize: "16px",
      width: "70%",
      marginLeft: "-1px",
    }}
  />
</div>

    <section className=''>
      
      <div className='p-10 mx-auto'>
        <div className='grid md:grid-cols-2 lg:grid-cols-5 gap-4 lg:gap-9'>
        
  
        {filteredHouses.filter(house =>house.status === 'For Rent').map((house, index) => (
          <Link to={`/property/${house.id}`} key={index}>
            <House house={house} />
          </Link>
        ))}
        </div>
      </div>
    </section>
    </>
  )
}

export default Buy